import React, { Component } from 'react';
import { useEffect } from 'react'


import '../../css/mainserv.css';
// import Footer from '../../components/Footer/footer.js/index.js';
import { Link, Route } from 'react-router-dom';
import { MDBCarousel, MDBCarouselInner, MDBCarouselItem, MDBView, MDBContainer } from "mdbreact";
import imag1 from '../../images/laptops.jpeg';
import imag2 from '../../images/laplaser.jpeg';
import imag3 from '../../images/tmsw4.jpg';
import imag4 from '../../images/tmsw5.jpg';
import imag5 from '../../images/team.jpg';
import imag6 from '../../images/warehousefork.jpg';
import home from '../../images/air.jpg';
import pic5 from '../../images/TMS-Warehousefull.jpg'
import home2 from "../../images/ftz.png"


function Warehouse() {

  // Scroll to top on load
  useEffect(() => {
    window.scrollTo(0, 0)
  }, [])

  return (

    <>
      {/* Container */}
      <div className='container-fluid navspace'>


        <div className="row">
          <div className="col-12 text-center">
            <div className="page-header">
              <h1 id="timeline" className="blue-text"><strong>Warehouse</strong></h1>
              <p className="font-italic"><h2>Miami Facility</h2></p>
            </div>
          </div>
        </div>

        <MDBCarousel
          activeItem={1}
          length={0}
          showControls={false}
          showIndicators={true}
          className="z-depth-1"
        >
          <MDBCarouselInner>
            <MDBCarouselItem itemId="1">
              <MDBView>
                <img
                  className="img-fluid m-x-auto d-block img-responsive"
                  src={pic5}
                  alt="First slide"
                />
              </MDBView>
            </MDBCarouselItem>
          </MDBCarouselInner>
        </MDBCarousel>

        {/* <div className="row">
        <div className="col">
          <a href="#">
            <img className="img-fluid rounded mb-3 mb-md-0" src={home2} alt="" width="100%" />
          </a>
 
        </div>
      </div> */}
        <br></br>
        <b></b>
        <b></b>
        {/* <div className="row">
        <div className="col">
          <a href="#">
            <img className="img-fluid rounded mb-3 mb-md-0" src={home} alt="" width="100%" />
          </a>

        </div>
      </div> */}

        <div className="row">
          <div className="col-sm-4 mb20"><img className="img-fluid m-x-auto d-block img-responsive" src={imag5} alt="" />

          </div>
          <div className="col-sm-4 mb20"><img className="img-fluid m-x-auto d-block img-responsive" src={imag1} alt="" />

          </div>
          <div className="col-sm-4 mb20"><img className="img-fluid m-x-auto d-block img-responsive" src={imag2} alt="" />

          </div>
        </div>
        <p></p>
        <div className="row">
          <div className="col-sm-4 mb20"><img className="img-fluid m-x-auto d-block img-responsive" src={imag3} alt="" />

          </div>
          <div className="col-sm-4 mb20"><img className="img-fluid m-x-auto d-block img-responsive" src={imag4} alt="" />

          </div>
          <div className="col-sm-4 mb20"><img className="img-fluid m-x-auto d-block img-responsive" src={imag6} alt="" />

          </div>
        </div>
      </div>
      <br></br>
      {/* <Footer /> */}
    </>
  );
}

export default Warehouse;